@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
  margin: 0;
  background-color: #0d0212;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
}

.backgroundImage {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-size: cover;
    background-position: center;
}

.langSwitch {
    position: absolute;
    font-size: 1.5rem;
    color: white;
    right: 15px;
    top: 15px;
    z-index: 1;
}

.map {
    width: 1000px;
    margin-bottom: 50px;
    position: relative;
}

.map img {
    object-fit: contain;
    max-width: 100%;
}

.kioskGlow {
    position: absolute;
    -webkit-animation-name: glowPulse;
            animation-name: glowPulse;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

@-webkit-keyframes glowPulse {
    0%   {opacity: 0;}
    50%  {opacity: 1;}
    100% {opacity: 0;}
}

@keyframes glowPulse {
    0%   {opacity: 0;}
    50%  {opacity: 1;}
    100% {opacity: 0;}
}

.mobileKiosk {
    position: absolute;
    width: 100%;
    padding-top: 30px;
    padding-bottom: 50px;
}

.mobileKiosk h2 {
    color: white;
    border-bottom: solid white;
    border-radius: 2px;
    max-width: 95%;
    margin: 0 auto;
    margin-top: 20px;
}

.richPreview {
    display: flex;
    align-items: center;
    position: relative;
    padding: 10px;
    width: 450px;
    height: 100px;
    background-color: white;
    border-radius: 5px;
    box-sizing: border-box;
    transition: all 200ms;
}

.richPreview.mobile {
    max-width: 90%;
    margin: 0 auto;
    margin-top: 20px;
    box-sizing: content-box;
}

.richPreview img {
    max-height: 90%;
    max-width: 72px;
    object-fit: contain;
    border-radius: inherit;
}

.richPreview p {
    flex: 1 1;
    margin-left: 10px;
}

a {
    text-decoration: none;
}

h2, p {
    margin: 0;
}

h2 {
    margin-bottom: 3px;
}

.hide {
    opacity: 0;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}
body {
    font-family: 'Lato', sans-serif;
}

h2 {
    font-size: 2.25rem;
}

p {
    font-size: 1.25rem;
}

li {
    font-size: 1.25rem;
}

a {
    color: inherit;
}

figcaption {
    font-size: 0.625rem;
}
